<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ this.orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ this.orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>
        <div class="cart__block">
          <orderInfoProducts :products="this.orderInfo.basket.items"/>
          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>{{ this.orderInfo.basket.items.length }}</b> 
               <b>{{ this.orderInfo.totalPrice | numberFormat }} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import orderInfoProducts from '@/components/orderInfoProducts.vue';
import numberFormat from '@/helpers/numberFormat';

export default {
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo;
    },
  },
  components: { orderInfoProducts },
  filters: {
    numberFormat,
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo === this.$route.params.id) {
      return false;
    }

    return this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};
</script>
