<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        3 
      </span>
    </div>

    <baseLoader v-if="this.isOrderSending" text="   ..."/>

    <section class="cart" v-if="!this.isOrderSending">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <baseFormText
              title=""
              :error="formError.name"
              placeholder="   "
              v-model="formData.name"
            />

            <label class="form__label">
              <input class="form__input" v-model="formData.address" type="text" placeholder="  ">
              <span class="form__value"> </span>
              <span class="form__error" v-if="formError.address">{{ formError.address }}</span>
            </label>

            <label class="form__label">
              <input class="form__input" v-model="formData.phone" type="tel" placeholder="  ">
              <span class="form__value"></span>
              <span class="form__error" v-if="formError.phone">{{ formError.phone }}</span>
            </label>

            <label class="form__label">
              <input class="form__input" v-model="formData.email" type="email" placeholder="  Email">
              <span class="form__value">Email</span>
              <span class="form__error" v-if="formError.email">{{ formError.email }}</span>
            </label>

            <baseFormTextarea
              title="  "
              :error="formError.comment"
              placeholder=" "
              v-model="formData.comment"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                     <b>500 </b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>
        <div class="cart__block">
          <orderInfoProducts :products="this.cartDetailProducts"/>
          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>{{ this.cartProductLength }}</b> 
               <b>{{ this.cartTotalPrice | numberFormat }} </b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
             
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import baseFormText from '@/components/baseFormText.vue';
import baseFormTextarea from '@/components/baseFormTextarea.vue';
import orderInfoProducts from '@/components/orderInfoProducts.vue';
import baseLoader from '@/components/baseLoader.vue';
import API_BASE_URL from '@/config';
import axios from 'axios';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      isOrderSending: false,
    };
  },
  computed: {
    ...mapGetters(['cartDetailProducts', 'cartTotalPrice', 'cartProductLength']),
  },
  components: {
    baseFormText, baseFormTextarea, orderInfoProducts, baseLoader,
  },
  filters: {
    numberFormat,
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.isOrderSending = true;
      clearTimeout(this.sendOrderTimer);
      this.sendOrderTimer = setTimeout(() => {
        axios.post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
          .then((response) => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .then(() => { this.isOrderSending = false; });
      }, 2000);
    },
  },
};
</script>
