<template>
  <baseLoader v-if="productLoading" text=" ..."/>
  <baseErrorLoad v-else-if="productLoadingFailed" @loadSmth="this.loadProduct"/>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{ product.category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="product.image.file.url"
            :alt="product.title"
          >
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | numberFormat}} 
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">:</legend>
              <baseColors
                :colors="colors"
                :colorsId.sync="localColor"
                class="productColor"
              />
            </fieldset>

            <div class="item__row">
              <baseCounter :amount.sync="currentAmount"/>

              <button class="button button--primery" type="submit" :disabled="productAddSending">
                 
              </button>
            </div>

            <div class="product-add-text" v-if="productAdded">   </div>
            <baseLoader v-if="productAddSending" text="   ..."/>

          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
                
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
             GPS, , BEIDOU Galileo  QZSS<br>
              <br>
              Bluetooth Smart, ANT+  Wi-Fi<br>
              <br>
          </p>

          <a href="#">
             
          </a>

          <h3> ?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS   ,
                 ,    .
             Wahoo ELEMNT BOLT GPS     ANT+,
                 .    ,
                 .
                      ,
                  .    ,
                  .       
                .
          </p>

          <h3></h3>

          <p>
             Wahoo ELEMNT BOLT  .
                74,6 x 47,3 x 22,1 .     .
                 .
                    56 .
                 ,
                    :
            ,   ,   .. (     ).
                   IPX7.  ,     ,
                ( 30 )        1 .
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import API_BASE_URL from '@/config';
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import baseColors from '@/components/baseColors.vue';
import baseCounter from '@/components/baseCounter.vue';
import baseLoader from '@/components/baseLoader.vue';
import baseErrorLoad from '@/components/baseErrorLoad.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  data() {
    return {
      currentAmount: 1,

      productData: null,

      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false,
    };
  },
  components: {
    baseColors, baseCounter, baseLoader, baseErrorLoad,
  },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
    }),
    productAmount() {
      return this.currentAmount;
    },
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category.id;
    },
    colors() {
      return this.productData.colors;
    },
    localColor: {
      get() {
        return this.productData.colors[0].id;
      },
      set(value) {
        return value;
      },
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({ productId: this.product.id, amount: this.currentAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => {
          this.productData = response.data;
        })
        .catch(() => { this.productLoadingFailed = true; })
        .then(() => { this.productLoading = false; });
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>

<style>
  li.colors__item {
    --border-color: white;
  }
  .loader .container {
    min-height: unset;
    margin: unset;
  }
  .product-add-text {
    margin-top: 35px;
    font-size: 1.3em;
  }
</style>
